.btn {
  padding: 0.375rem 2rem;
}

.btn.btn-primary {
  color: #fff;
  background-color: rgb(51, 102, 153);
  border-color: rgb(51, 102, 153);
}

.btn.btn-primary:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: rgb(42, 90, 138);
  border-color: rgb(42, 90, 138);
}

.btn.btn-primary:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: rgb(42, 90, 138);
  border-color: rgb(42, 90, 138);
}

.btn.btn-primary:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(51, 102, 153, 0.5);
}


.btn.btn-secondary {
  color: #fff;
  background-color: rgb(134, 187, 216);
  border-color: rgb(134, 187, 216);
}

.btn.btn-secondary:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: rgb(112, 166, 196);
  border-color: rgb(112, 166, 196);
}

.btn.btn-secondary:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: rgb(112, 166, 196);
  border-color: rgb(112, 166, 196);
}

.btn.btn-secondary:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(134, 187, 216, 0.5);
}

